import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


export default new Router({
    routes: [
        {
            path: '/',
            component: resolve => require(['~/views/layout/App.vue'], resolve),
            meta: {
                title: '',
                keepAlive: false
            },
            children: [
                {
                    path: '/',
                    name: 'home',
                    meta: {
                        title: '',
                        keepAlive: true
                    },
                    component: resolve => require(['~/views/home/Article.vue'], resolve),
                },
                {
                    path: '/collection',
                    name: 'collection',
                    meta: {
                        title: '',
                        keepAlive: true
                    },
                    component: resolve => require(['~/views/collection/Index.vue'], resolve),
                },
                {
                    path: '/me',
                    name: 'me',
                    meta: {
                        title: '',
                        keepAlive: true
                    },
                    component: resolve => require(['~/views/me/Index.vue'], resolve),
                },
            ]
        },
        {
            path: '/login',
            name: 'Login',
            meta: {
                title: '',
                keepAlive: false
            },
            components: {
                blank: resolve => require(['~/views/login/Login.vue'], resolve),
            }
        },

    ]
})





